import {LitElement, html, css} from 'lit';
import {customElement, property} from 'lit/decorators.js';

@customElement('view-battleground')
export class BattleGround extends LitElement {
  static styles = css`
    :host {
      display: grid;
      padding: 20px;
      position: relative;
      box-sizing: border-box;
      width: 100%;
      height: 70vh;
      border: 1px solid #aaa;
    }
    .monster-side {
      display: grid;
      width: 100%;
      height: 200px;
      background-color: #eee;
      position: absolute;
      top: 100px;
      left: 0;
    }
    .player-side {
      display: grid;
      width: 100%;
      height: 200px;
      background-color: #eee;
      position: absolute;
      bottom: 50px;
      left: 0;
    }
  `;

  @property({type: Array<String>()})
  monsters = [];

  @property({type: Array<String>()})
  player = []; // player side may contain plar and his creatures

  @property({type: String})
  name = "冰冷之原";
  render() {
    return html`
      <h3>战斗地图: ${this.name}</h3>
      <div class="monster-side"></div>
      <div class="player-side"></div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'view-battleground': BattleGround;
  }
}
